.background {
    background-image: url("../Components/Images/background.jpg");
    height: 100vh;
    width: 100%;
    background-size: cover;
    background-position: center;
}

.intro{
    font-size: 25px;
    padding: 20px;
    color:#385170; ;
    margin-top: 40%;
    grid-column: 4/8;
}

.intro h1 ,h3 ,p{
    padding: 5px 0px;
}
.intro h1{
    color: #142d4c; ;
}
main{
    height: 100%;
}

@media screen and (max-width:1100px) {
    .intro{
        grid-column: 2/8;
        
    }
 
    .main{
        display: flex;
        justify-content: center;
        align-items: center;
    }
}